import React, { FormEvent, useState } from "react";
import {
  TextField,
  FormControlLabel,
  Checkbox,
  Grid,
  Button,
  MenuItem,
  Typography,
} from "@mui/material";

const FormularioConsulta = () => {
  const [formData, setFormData] = useState({
    paciente: "",
    fecha: "",
    motivoConsulta: "",
    presentoDolor: false,
    diagnostico: "",
    comentarioAdicional: "",
    quienAtendio: "",
    pagoRecibido: 0,
    tipoPago: "",
  });

  const handleChange = (
    event: React.ChangeEvent<
      HTMLInputElement | HTMLTextAreaElement | { name?: string; value: unknown }
    >
  ) => {
    const { name, value, type, checked } = event.target as HTMLInputElement;
    setFormData({
      ...formData,
      [name!]: type === "checkbox" ? checked : value,
    });
  };

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    console.log(formData); // Puedes manejar el envío aquí
  };

  return (
    <div style={{ overflow: "auto", maxHeight: "70vh" }}>
      <Typography variant="h4">Información cita</Typography>
      <form onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              label="Paciente"
              name="paciente"
              value={formData.paciente}
              onChange={handleChange}
              required
            />
          </Grid>

          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              label="Fecha"
              name="fecha"
              type="date"
              InputLabelProps={{ shrink: true }}
              value={formData.fecha}
              onChange={handleChange}
              required
            />
          </Grid>

          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Motivo de la Consulta"
              name="motivoConsulta"
              value={formData.motivoConsulta}
              onChange={handleChange}
              required
            />
          </Grid>

          <Grid item xs={12}>
            <FormControlLabel
              control={
                <Checkbox
                  checked={formData.presentoDolor}
                  onChange={handleChange}
                  name="presentoDolor"
                  color="primary"
                />
              }
              label="¿Presentó Dolor?"
            />
          </Grid>

          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Diagnóstico"
              name="diagnostico"
              value={formData.diagnostico}
              onChange={handleChange}
              required
            />
          </Grid>

          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Comentario Adicional"
              name="comentarioAdicional"
              multiline
              rows={4}
              value={formData.comentarioAdicional}
              onChange={handleChange}
            />
          </Grid>

          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              label="¿Quién Atendió?"
              name="quienAtendio"
              value={formData.quienAtendio}
              onChange={handleChange}
              required
            />
          </Grid>

          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              label="Pago Recibido"
              name="pagoRecibido"
              type="number"
              value={formData.pagoRecibido}
              onChange={handleChange}
              required
            />
          </Grid>

          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              label="Tipo de Pago"
              name="tipoPago"
              select
              value={formData.tipoPago}
              onChange={handleChange}
              required
            >
              <MenuItem value="Efectivo">Efectivo</MenuItem>
              <MenuItem value="Tarjeta">Tarjeta</MenuItem>
              <MenuItem value="Transferencia">Transferencia</MenuItem>
            </TextField>
          </Grid>

          <Grid item xs={12}>
            <Button variant="contained" color="primary" type="submit">
              Enviar
            </Button>
          </Grid>
        </Grid>
      </form>
    </div>
  );
};

export default FormularioConsulta;
